import React, { useState } from "react";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import ExpandLessIcon from "@mui/icons-material/ExpandLess";
import CloudDoneIcon from "@mui/icons-material/CloudDone";
import GitHubIcon from "@mui/icons-material/GitHub";

const ProjectCard = ({ data }) => {
  const [more, setmore] = useState(false);
  const handleShowMore = () => {
    setmore(!more);
  };
  return (
    <div className="mt-4 p-2 bg-cardColor flex flex-col justify-center rounded-lg border-2 border-rose lg:w-3/12">
      {/* Card-Body */}
      {/* Image */}
      <div className="block mx-auto w-6/6">
        <img
          className="block rounded-lg mx-auto"
          src={data.imgUrl}
          // src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS6sv4tokbDlZpHYfb-X6QJQcwP221peTC14g&usqp=CAU"
          alt=""
        />
      </div>
      {/* Footer */}
      <div className="p-2 bg-cardColor">
        <div>
          <h1 className="text-lg font-semibold bg-cardColor text-center">
            {data.projectname}
          </h1>
        </div>
        <div className="pt-1 flex justify-evenly bg-cardColor">
          <a href={data.webUrl} target="_blank" rel="noreferrer">
            <CloudDoneIcon />
          </a>
          <a href={data.github} target="_blank" rel="noreferrer">
            <GitHubIcon />
          </a>
          {more ? (
            <ExpandLessIcon onClick={handleShowMore} className="bg-cardColor" />
          ) : (
            <ExpandMoreIcon onClick={handleShowMore} className="bg-cardColor" />
          )}
        </div>
      </div>
      <div className="w-6/6 lg:w-12/12">
        <span hidden={!more}>{data.projectDescription}</span>
      </div>
    </div>
  );
};

export default ProjectCard;
